body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  color: white; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  position: relative;
  background: url(../img/hand.png) no-repeat 0 30%, url(../img/back-mobile@1x.jpg) no-repeat 0 0;
  background-size: 100%, 100%; }

.container--top {
  position: relative; }
  .container--top::after {
    content: "";
    position: absolute;
    width: 100vw;
    height: 65px;
    top: 0;
    left: 0;
    background-color: rgba(28, 37, 50, 0.5); }

.page-header__wrapper {
  display: flex;
  flex-direction: column;
  position: relative; }

.logo-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 21px;
  padding-bottom: 21px;
  z-index: 10; }

.toggle {
  display: block;
  cursor: pointer;
  width: 50px;
  height: 24px;
  background-color: inherit;
  border: none;
  padding: 0;
  margin: 0; }

.main-nav {
  z-index: 10;
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100vw;
  top: 65px;
  margin-left: -20px;
  background-color: #283645; }
  .main-nav__list {
    padding: 0;
    margin: 0; }

.site-list__item {
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
  list-style-type: none;
  border-bottom: 1px solid black;
  border-top: 1px solid #485461; }
  .site-list__item a {
    color: white; }

.tablet,
.desktop {
  display: none; }

.container--info {
  margin-top: 462px;
  background-color: #1d2631; }

.info__download {
  padding-top: 28px;
  padding-bottom: 28px;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .info__download .logos {
    display: flex;
    justify-content: space-around;
    align-items: baseline;
    margin-top: 40px;
    margin-bottom: 30px; }
  .info__download--description {
    text-align: center; }

.button {
  border: 4px solid white;
  background-color: inherit;
  color: white;
  box-sizing: border-box;
  padding: 20px 0;
  border-radius: 30px; }

.info__title {
  display: none; }

@media (min-width: 660px) {
  .mobile,
  .desktop {
    display: none; }
  .tablet {
    display: block; }
  .page-header {
    background: url(../img/hand.png) no-repeat 50% 120%, url(../img/back-mobile@1x.jpg) no-repeat 50% 50%;
    background-size: 470px, cover; }
  .container--top::after {
    height: 100px; }
  .container--info {
    margin-top: 100px;
    background-color: inherit; }
  .info__wrapper {
    width: 300px; }
  .info__title {
    display: block;
    text-align: center; }
  .info__download {
    padding-bottom: 110px; }
  .main-nav {
    top: 100px; } }

@media (min-width: 1200px) {
  .mobile,
  .tablet {
    display: none; }
  .desktop {
    display: block; }
  .page-header__wrapper {
    flex-direction: row; }
  .main-nav {
    display: flex;
    padding-top: 21px;
    padding-bottom: 21px;
    position: static;
    margin-left: 170px;
    width: 100%;
    background-color: inherit; }
  .site-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
    .site-list__item {
      border: none; }
  .toggle {
    display: none; } }

.container {
  width: auto;
  padding: 0 20px;
  position: relative; }

@media (min-width: 660px) {
  .container {
    width: auto; } }

@media (min-width: 1200px) {
  .container {
    width: 940px;
    padding: 0 130px; } }

.none {
  display: none; }
